@import '../../styles/base/variable';
@import '../../styles/base/mixins';


.accordion-wrapper {
    border: 1px solid #D1D3D6;
    border-radius: 2px;
    margin: 24px 0;

  button.accordion {
    width: 100%;
    border: none;
    outline: none;
    text-align: left;
    padding: 15px 20px;
    cursor: pointer;
    transition: background-color 0.2s linear;
    font: var(--unnamed-font-style-normal) normal var(--unnamed-font-weight-normal) var(--unnamed-font-size-20)/var(--unnamed-line-spacing-25) var(--unnamed-font-family-muli);
    letter-spacing: var(--unnamed-character-spacing-0);
    color: var(--unnamed-color-172026);
    background-color: #FFFFFF;
    border-bottom: 1px solid #D1D3D6;
    border-radius: 2px;
  }
  
  button.accordion:after {
    background-image: url('./images/arrow-down-grey.svg');
    float: right;
  }
  
  
  .accordion-content {
    background-color: white;
    border-left: 1px solid whitesmoke;
    border-right: 1px solid whitesmoke;
    padding: 0 20px;
    max-height: 0;
    overflow: hidden;
    transition: max-height 0.2s ease-in-out;
  }

} 


@include desktop {

  .accordion-wrapper {
    margin: 40px 20px 64px 0;
  }

}